@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: all 0.2s ease;
}
*::-webkit-scrollbar {
  width: 9px;
  height: 8px;
  background-color: #dfdfdf;
  border-radius: 10px;
}
@media only screen and (max-width: 600px) {
  *::-webkit-scrollbar {
    width: 8px;
    height: 6px;
  }
}
*::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, rgba(36, 63, 214, 0.8) 0, rgba(52, 169, 247, 0.8) 100%);
  border-radius: 10px;
}
*::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg, #243fd6 0, #34a9f7 100%);
}

h6 {
  color: #455cd9;
  font-size: 18px;
  font-weight: 600;
  padding: 0;
  margin-top: 5px;
}

.react-tooltip {
  transition: all 0.2s ease !important;
}

.App {
  min-height: 110vh;
  display: flex;
  flex-direction: column;
}

.Toastify__toast-body {
  font-family: "Nunito", sans-serif;
}

body {
  font-family: "Nunito", sans-serif;
  background: rgb(217, 222, 247);
  background: linear-gradient(180deg, rgba(141, 152, 207, 0.28) 0, rgb(240, 243, 255) 100%);
  color: #343434;
}

.disable-scroll {
  overflow: hidden;
}

.skeleton,
.skeleton-without-animation,
.hotevents-skeleton {
  background-color: #bebebe;
  -webkit-box-shadow: 0px 0px 15px -3px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 0px 15px -3px rgba(0, 0, 0, 0.2);
  height: 355px;
  width: 90%;
  margin: 10px auto;
  border-radius: 10px;
  border-radius: 10px;
  padding: 10px;
  animation: infiniteAnimation 2s infinite;
}
.skeleton div,
.skeleton-without-animation div,
.hotevents-skeleton div {
  width: 100%;
  margin: 0 auto;
  height: 30px;
  background-color: #ebebeb;
  border-radius: 10px;
  margin-bottom: 10px;
  background-color: rgba(150, 146, 146, 0.4862745098);
}
.skeleton div:nth-child(1),
.skeleton-without-animation div:nth-child(1),
.hotevents-skeleton div:nth-child(1) {
  height: 75%;
  margin: 0 auto 10px auto;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
.skeleton div:nth-child(3),
.skeleton-without-animation div:nth-child(3),
.hotevents-skeleton div:nth-child(3) {
  width: 70%;
  margin: 0 auto;
}

.hotevents-skeleton {
  height: 150px;
}
.hotevents-skeleton div:nth-child(1) {
  height: 69%;
}

.skeleton-without-animation {
  animation: none;
  background-color: rgba(190, 190, 190, 0.17);
}
.skeleton-without-animation div {
  background-color: rgba(150, 146, 146, 0.174);
}

/* hide arrows
 Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none !important;
  margin: 0 !important;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield !important;
}

@keyframes infiniteAnimation {
  0%, 100% {
    opacity: 0.5;
  }
  50% {
    opacity: 1;
  }
}
small {
  opacity: 0.8;
}

.navlink-active {
  background-color: rgba(255, 255, 255, 0.115);
}

.highlighted {
  fill: rgba(244, 206, 70, 0.794);
}

.done {
  fill: rgb(110, 255, 0);
}

rect:hover,
polygon:hover {
  filter: brightness(30%);
}

