.nav-search-wrapper {
  position: relative;
  width: 100%;
  max-width: 231px;
}
.nav-search-wrapper form {
  position: relative;
}
.nav-search-wrapper input {
  background-color: #fff;
  border: none;
  outline: none;
  color: #455cd9;
  padding: 5px 20px 5px 10px;
  border-radius: 10px;
  font-size: 14px;
  width: 100%;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.25);
}
.nav-search-wrapper input:focus {
  outline: none;
}
.nav-search-wrapper input::placeholder {
  color: #455cd9;
}
@media only screen and (max-width: 450px) {
  .nav-search-wrapper input::placeholder {
    color: transparent;
  }
}
.nav-search-wrapper img {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  width: 17px;
}
.nav-search-wrapper .loader {
  width: 15px;
  height: 15px;
  border: 2px solid #576cdc;
  border-bottom-color: transparent;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
  position: absolute;
  right: 3px;
  top: 8px;
}
@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.search-nav-right li {
  width: 100%;
  margin-top: auto;
}
.search-nav-right li .nav-search-wrapper {
  max-width: 100%;
}

.profile-link {
  position: relative;
  text-align: center;
}
@media only screen and (max-width: 1199px) {
  .profile-link {
    width: 10%;
