  }
}
.profile-link .dropdown {
  animation: fade 0.3s ease-in-out;
  box-shadow: -2px 8px 50px 8px rgba(0, 0, 0, 0.35);
  -webkit-box-shadow: -2px 8px 50px 8px rgba(0, 0, 0, 0.35);
  -moz-box-shadow: -2px 8px 50px 8px rgba(0, 0, 0, 0.35);
